.scroll-list-container {
  position: relative;
  width: clamp(220px, 60vw, 500px);
  max-width: 100%;
}

.scroll-list {
  max-height: unset;
  overflow-y: auto;
  padding: clamp(4px, 1vw, 16px);
}

.scroll-list::-webkit-scrollbar {
  width: 8px;
}

.scroll-list::-webkit-scrollbar-track {
  background: #060010;
}

.scroll-list::-webkit-scrollbar-thumb {
  background: #271e37;
  border-radius: 4px;
}

.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.item {
  padding: clamp(6px, 1vw, 16px);
  background-color: #170d27;
  border-radius: 8px;
  margin-bottom: clamp(4px, 1vw, 1rem);
}

.item.selected {
  background-color: #271e37;
}

.item-text {
  color: white;
  margin: 0;
  font-size: var(--size-body, 1.1em) !important;
}

@media only screen and (max-height: 700px) {
  .item-text {
    font-size: calc(var(--size-body, 1.1em) * 0.9) !important;
  }
}
@media only screen and (max-height: 620px) {
  .item-text {
    font-size: calc(var(--size-body, 1.1em) * 0.8) !important;
  }
}

.top-gradient {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 50px;
  background: linear-gradient(to bottom, #060010, transparent);
  pointer-events: none;
  transition: opacity 0.3s ease;
}

.bottom-gradient {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100px;
  background: linear-gradient(to top, #060010, transparent);
  pointer-events: none;
  transition: opacity 0.3s ease;
}
